/* purgecss start ignore */

body{
  box-sizing:border-box;
  font-family:"Raleway", sans-serif;
  max-width:100%;
  overflow-x:hidden;
  --tw-bg-opacity:1;
  background-color:rgba(0, 0, 0, var(--tw-bg-opacity));
  padding:1.25rem;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

body .lines{
  position:absolute;
  top:0px;
  left:0px;
  margin-left:auto;
  margin-right:auto;
  height:100%;
  width:100%;
  padding-left:1.25rem;
  padding-right:1.25rem
}

@media (min-width: 768px){
  body .lines{
    padding-left:5rem;
    padding-right:5rem
  }
}

body .lines{
  z-index:3;
  display:grid;
  grid-template-columns:repeat(4, 1fr)
}

body .lines .line{
  border-left:0.5px solid;
  height:100%;
  --tw-border-opacity:1;
  border-color:rgba(17, 24, 39, var(--tw-border-opacity))
}

body .lines .line:last-child{
  border-right:0.5px solid;
  --tw-border-opacity:1;
  border-color:rgba(17, 24, 39, var(--tw-border-opacity))
}

header{
  display:flex;
  flex-direction:row;
  justify-content:space-between
}

header span{
  display:block;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

header span.hamburger span{
  width:30px;
  height:2px;
  position:relative;
  margin-top:0.75rem;
  margin-bottom:0.75rem;
  display:block;
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity))
}

header span.close-icon{
  position:relative;
  content:""
}

header span.close-icon::before,header span.close-icon::after{
  content:"";
  width:40px;
  height:2px
}

header span.close-icon::before, header span.close-icon::after{
  position:absolute;
  top:0.75rem;
  left:-0.25rem;
  margin-top:0.75rem;
  margin-bottom:0.75rem;
  display:none;
  --tw-translate-x:0;
  --tw-translate-y:0;
  --tw-rotate:0;
  --tw-skew-x:0;
  --tw-skew-y:0;
  --tw-scale-x:1;
  --tw-scale-y:1;
  transform:translateX(var(--tw-translate-x)) translateY(var(--tw-translate-y)) rotate(var(--tw-rotate)) skewX(var(--tw-skew-x)) skewY(var(--tw-skew-y)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
  --tw-rotate:45deg;
  --tw-bg-opacity:1;
  background-color:rgba(255, 255, 255, var(--tw-bg-opacity))
}

header span.close-icon::after{
  --tw-rotate:-45deg
}

header span .show::after, header span .show::before{
  display:block
}

nav{
  position:absolute;
  display:grid;
  height:80%;
  width:100%;
  grid-template-columns:repeat(1, minmax(0, 1fr));
  place-items:center
}

nav ul{
  display:none;
  flex-direction:column;
  align-items:center
}

nav ul li{
  display:block
}

nav ul li:hover{
  border-bottom-width:1px
}

nav ul li{
  --tw-border-opacity:1;
  border-color:rgba(209, 213, 219, var(--tw-border-opacity));
  padding-top:0.75rem;
  padding-bottom:0.75rem;
  font-size:3rem;
  line-height:1;
  font-weight:100;
  text-transform:uppercase;
  letter-spacing:0.1em;
  --tw-text-opacity:1;
  color:rgba(209, 213, 219, var(--tw-text-opacity));
  opacity:0;
  transition-property:opacity;
  transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration:150ms
}

nav ul li .show{
  opacity:1
}

nav ul .show{
  display:flex
}

main{
  width:100%;
  margin-right:auto;
  margin-left:auto
}

@media (min-width: 640px){
  main{
    max-width:640px
  }
}

@media (min-width: 768px){
  main{
    max-width:768px
  }
}

@media (min-width: 1024px){
  main{
    max-width:1024px
  }
}

@media (min-width: 1280px){
  main{
    max-width:1280px
  }
}

@media (min-width: 1536px){
  main{
    max-width:1536px
  }
}

main{
  position:relative;
  overflow-x:hidden
}

main img{
  max-width:100%;
  width:100%;
  height:100%
}

main .hero{
  overflow-x:hidden;
  z-index:100
}

main .hero .section{
  width:100%;
  display:grid;
  grid-template-columns:1fr 1.5fr 1fr;
  grid-template-rows:2fr 1fr;
  justify-content:center;
  margin-top:5rem;
  margin-bottom:5rem;
  padding-right:3.5rem
}

@media (min-width: 768px){
  main .hero .section{
    grid-template-rows:1fr;
    grid-template-columns:1fr 1.5fr 1fr 2fr;
    align-items:center;
    margin-right:0px
  }
}

main .hero .section .content{
  grid-column:2 / -1;
  grid-row:1 / -1;
  display:grid;
  grid-template-columns:1fr;
  grid-template-rows:200px 1fr;
  align-items:flex-start;
  position:relative
}

@media (min-width: 768px){
  main .hero .section .content{
    grid-template-columns:2fr 1fr 2fr;
    grid-template-rows:1fr
  }
}

main .hero .section .content .main-img{
  grid-column:1 / 3;
  grid-row:1/ 2;
  z-index:5;
  opacity:0;
  height:100%;
  width:100%;
  animation:show 0.4s ease-in-out forwards
}

@media (min-width: 768px){
  main .hero .section .content .main-img{
    grid-row:1/ -1
  }
}

main .hero .section .content .main-img img{
  z-index:-1
}

main .hero .section .content .text{
  grid-row:2 / -1;
  grid-column:1 / -1;
  z-index:10;
  text-align:right;
  font-weight:100;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

main .hero .section .content .text h3{
  grid-column:2 / -1;
  grid-row:2 / -1;
  z-index:10;
  align-self:flex-start;
  font-size:3rem;
  line-height:1;
  font-weight:100
}

@media (min-width: 1024px){
  main .hero .section .content .text h3{
    font-size:6rem;
    line-height:1
  }
}

@media (min-width: 1280px){
  main .hero .section .content .text h3{
    font-size:8rem;
    line-height:1
  }
}

@media (min-width: 768px){
  main .hero .section .content .text h3{
    grid-row:1 / -1;
    grid-column:4 / -1
  }
}

main .hero .section .content .text h3 span{
  display:block;
  animation:showOpacity 0.3s linear forwards;
  opacity:0;
  transform:translate(100px, 0)
}

main .hero .section .content .text h3 span:nth-child(2){
  animation-delay:0.2s
}

main .hero .section .content .text p{
  animation-delay:0.3s;
  margin-top:1.25rem;
  margin-bottom:4rem;
  animation:showOpacity 0.3s linear forwards;
  font-size:1.5rem;
  line-height:2rem;
  letter-spacing:0.025em
}

@media (min-width: 768px){
  main .hero .section .content .text p{
    margin-left:auto;
    width:75%
  }
}

@media (min-width: 768px){
  main .hero .section .content .text{
    grid-column:2 / -1;
    grid-row:1 / -1
  }
}

main .hero .section .content::before{
  content:"";
  position:absolute;
  left:-30%;
  width:30%;
  height:100%;
  z-index:2;
  background-color:black
}

main .hero .section .img-overlay{
  grid-column:1 /3;
  grid-row:1 / -1;
  z-index:10;
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content
}

